import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid  from '@mui/material/Grid';


import bappi from '../../assets/image1mob.jpg';
import lata from '../../assets/image2mob.jpg';

export default function ImgCard() {
  return (
    <Grid container spacing={4} justifyContent="center">
        <Grid item>
            <Card sx={{ maxWidth: 345 }}>
                <CardMedia
                    component="img"
                    alt="bappi lahiri"
                    height="140"
                    image={bappi}
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div" style={{color:"black"}}>
                    ♬ ʙᴀᴘᴘɪ ʟᴀʜᴀʀɪ 🎤
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                    Alokesh Lahiri (27 November 1952 – 15 February 2022), popularly known as Bappi Lahiri, was an Indian singer, composer, politician and record producer. 
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button size="small"><a href='https://bappilahiri.fortribute.com/'>Visit</a></Button>
                </CardActions>
            </Card>
        </Grid>
        <Grid item>
            <Card sx={{ maxWidth: 345 }}>
                <CardMedia
                    component="img"
                    alt="bappi lahiri"
                    height="140"
                    image={lata}
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div" style={{color:"black"}}>
                    🎤 𝘓𝘢𝘵𝘢 𝘔𝘢𝘯𝘨𝘦𝘴𝘩𝘬𝘢𝘳 🎤
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                    Lata Mangeshkar (born as Hema Mangeshkar; 28 September 1929 – 6 February 2022) was an Indian playback singer and occasional music composer.
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button size="small"><a href="http://latamangeshkar.fortribute.com/">Visit</a></Button>
                </CardActions>
            </Card>
        </Grid>
    </Grid>
  );
}
