import Grid  from '@mui/material/Grid';
import { Box } from '@mui/system';
import Paper from '@mui/material/Paper';
import ImgCard from '../component/Card/card';
import "./layout.css"

const Layout2 = () => {

    return(
        <Grid container sx={{
            p:5
        }}>
            <Grid item xs={12} sx={{m:0,p:0}}>
                <Box sx={{
                    p:0,
                    m:0
                }}>
                    <p className='layout2-heading'>🙏 Om Shanti 🙏</p>
                </Box>
            </Grid>
            <Grid item xs={12} sx={{m:0,p:0}}>
                <Box sx={{
                    m:{
                        xs: 2,
                        sm: 2,
                        md: 6,
                        lg: 6,
                        xl: 6
                    }
                }}>
                    <ImgCard/>
                </Box>
            </Grid>
        </Grid>
    );
}


export default Layout2